.contractsContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
     min-height: 100vh;
     h2{
         font-size: 4rem;
         color: #E5610E;
         font-family: DinRegular;
     }
     .cards{
         min-width: 80vw;
         max-width: 80vw;
        display: flex;
        flex-direction: row;    
        justify-content: center;
        align-items: center;
        section{     
            min-width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
        }
        .cardOne{
            min-height: 50vh;
            border-radius: 1vw;
            box-shadow: 3px 3px 3px 3px rgba(0, 0, 0, 0.2);
            display: flex;
            justify-content: flex-start;
            margin: 0;
            padding: 0;
            h3{
                margin: 0;
                padding: 0;
                color: #E58A0E;
                min-height: 12vh;
                min-width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                border-bottom: 2px solid rgba($color: #000000, $alpha: 0.1);
                font-size: 3rem;
                font-family: HelveticaNeue;
            }
            .middle{
                min-height: 18vh;
                min-width: 80%;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                align-items: center;
                label{
                    font-size: 1.8rem;
                    line-height: 2.2rem;
                    font-weight: 300;
                    color: #707070;
                    font-family: HelveticaNeue;
                }
            }
            .options{
                margin-top: 0;
                min-height: 15vh;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                label{
                    font-size: 1.8rem;
                    color: #E58A0E;
                    min-width: 10vw;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    font-family: HelveticaNeue;
                }                
            }
            h5{
                margin-top: 0;            
                display: flex;
                justify-content: flex-start;
                color: #707070;
                font-size: 1.2rem;
                font-family: HelveticaNeue;
            }
        }
        .cardTwo{
            max-width: 40%;
            min-width: 40%;
            min-height: 40vh;
            max-height: 40vh;
            border: 1px solid yellow;
           // background-color: #585757;
           border-top-right-radius: 1vw;
           border-bottom-right-radius: 1vw;
           background: linear-gradient(20deg, black 5%, #585757);
           display: flex;
           flex-direction: column;
            justify-content: center;
            align-items: center;
            h2{                
                color: #E58A0E;
                font-size: 2.5rem;
            }
            text{
                min-height: 15vh;
                color: #FFF;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: flex-end;
                padding:0;
                margin: 0;
                h5{
                    margin: 0;
                    min-height: 12vh;
                    max-height: 12vh;
                    font-size: 1.5rem;
                }
                h3{
                    margin: 0;
                    min-height: 12vh;
                    max-height: 12vh;
                    font-size: 10vh;
                }
                h1{
                    margin: 0;
                    min-height: 15vh;
                    max-height: 15vh;
                    font-size: 5rem;
                    font-size: 15vh;
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                }
                h4{
                    margin: 0;
                    min-height: 15vh;
                    font-size: 2rem;
                    display: flex;
                    justify-content: flex-end;
                    align-items: flex-end;
                }
            }
            .texttwo{
                width: 20vw;
                min-height: 15vh;
                display: flex;
                margin: 0;
                padding: 0;
                align-items:center;
                justify-content: space-around;
                flex-direction: column;
                label{
                    margin: 0;
                    padding: 0;
                    min-width: 100%;
                    text-align: center;
                }
                section{
                    min-width: 100%;
                    min-height: 5vh;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-around;
                    .boton1{
                        min-height: 50px;
                        min-width: 180px;
                        border: 1px solid #E5610E;
                        color: #E5610E;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 25px;
                        font-size: 1.2rem;
                        font-weight: 500;
                        cursor: pointer;
                    }
                    .boton2{
                        cursor: pointer;
                        min-height: 50px;
                        min-width: 180px;
                        border: 1px solid #E5610E;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 25px;
                        color: #E5610E;
                        font-size: 1.2rem;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}