
    .containerServices{
        min-height: 60vh;
        min-width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        label{
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 5vh;
            font-size: 1.5rem;
            color: #707070;
            
        }
        .title{
            font-size: 6rem;
            font-weight: 500;
            color: #E5610E;
        }
        .carruselServicesContainer{
            min-height: 20vh;
            min-width: 90%;
        }
        .Slider{
            margin-top: 2vh;
            div{
                align-items: center;
                img{
                    margin-left: 40%;
                }
                img:hover{
                 filter: saturate(20%);
                 fill: #E5610E;
                filter:  contrast(80% ) ;
                }
            }
        }

        .element{
        }
    }