
    .containerMaps{
        border: 1px solid green;
        background: linear-gradient(15deg, black 10%, #707070);
        min-height: 80vh;
        display: flex;
            flex-direction: row;
            .Maps{
                z-index: 3;
                border: 1px solid s;
                min-width: 60%;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 2s;
           
                
                .Fondo1,.Fondo2,.Fondo3,.Fondo4,.Fondo5,.Fondo6{
                    transition: all 2s;
                    transition-duration: 1s;
                    position: absolute;
                    margin-top: 0;
                    min-height: 70%;
                    min-width: 100%;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    animation: aparecer 2s;
                }
                .Fondo1{background-image:  url('../../../img/home/maps/Fondo1.svg');}
                .Fondo2{background-image:  url('../../../img/home/maps/Fondo2.svg');}
                .Fondo3{background-image:  url('../../../img/home/maps/Fondo3.svg');}
                .Fondo4{background-image:  url('../../../img/home/maps/Fondo4.svg');}
                .Fondo5{background-image:  url('../../../img/home/maps/Fondo5.svg');}
                .Fondo6{background-image:  url('../../../img/home/maps/Fondo6.svg');}
            }
            @keyframes aparecer {
                0%{
                    opacity: 0;
                }
                100%{
                    opacity: 1;
                }
            }
            .Options{
                z-index: 5;
                min-width: 40%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                h2{
                    color: #FFF;
                }
                label{
                    color: #707070;
                    font-size: 1.2rem;
                    font-family: HelveticaNeue;
                }
                h5{
                    color: #E5610E;
                    font-size: 1.3rem;
                    font-family: HelveticaNeue;
                }
            
            }    
    }