.headerContainer{
    position: relative;
    top: 0;
    z-index: 20;
    min-width: 100vw;
    max-width: 100vw;
    min-height: 8vh;
    max-height: 8vh;
    display: flex;
    flex-direction: row;
    .Logo{
        margin: 0;
        padding: 0;
        min-width: 15vw;
        max-height: 8vh;
        background-image: url('../../img/icons/LogoHeader.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 35%;
    }
    .navContainer{
        margin: 0;
        padding: 0;
        min-width: 45vw;
        max-width: 45vw;
        min-height: 8vh;
        display: flex;
        flex-direction: row;
        text-decoration: none;
        li{
            min-height: 100%;
            text-decoration: none;
            list-style: none;
            display: flex;
            align-items: center;
            margin-right: 2vw;
        }
        li:hover{
            color: #E5610E;
        }
    }
    .contactInformation{
        margin: 0;
        padding: 0;
        min-width: 40vw;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        text{

        }
        .iconsContainer{
            margin-right: 5vw;
            display: flex;
            flex-direction: row;
            img{
                margin-left: 1vw;
                margin-right: 1vw;
            }
        }
    }
}





