
.aboutContainer{
    min-width: 100vw;
    max-width: 100vw;
    

    .fondoCoche{
        min-width: 100vw;      
        width: 100vw;        
        height: 75vh;
        max-width: 100vw;
        display: flex;
        justify-content: flex-end;
        background-repeat: no-repeat;
        background: linear-gradient(45deg, black 40%, #707070);
        img{
            position: absolute;
            width: 70%;
        }
    }
    .clearlineContainer{
        display: flex;
        section{
            min-width: 50vw;
            max-width: 50vw;
        }
        .contentLeft{
            img{
                max-width: 100%;
            }
        }
        .contentRight{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h3{
                width: 70%;
                color: #E5610E;
                font-size: 3.5rem;
            }
            text{
                width: 70%;
                color: #707070;
                font-size: 1.2rem;
                text-align: justify;
                line-height: 1.8rem;
            
            }
        }
        
    }


   
}
