body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


@font-face {
  font-family: DinRegular;
  src: local('fonts'),
   url("./fonts/Fonts/D-DIN-PRO-400-Regular.otf") format("truetype");
}

@font-face {
  font-family: HelveticaNeue;
  src: local('fonts'),
   url("./fonts/Fonts/HelveticaNeue.ttc") format("truetype");
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',DinRegular,HelveticaNeue,
    monospace;
}
