.footerContainere{

    background-color: #2C2C2C;
    position: absolute;
    margin-bottom: 0;
    margin-left: 0;
    min-height: 30vh;
    min-width: 100vw;

    display: flex;
    flex-direction: row;

    margin: 0;
    padding: 0;
    justify-content: space-around;
    section, .navFooter{
        margin: 0;
        padding: 0;
        min-width: 30vw;
        min-height: 30vh;
        max-height: 30vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: #FFF;
        
    }
    .first{
            list-style: none;
            text-decoration: none;
         
                list-style: none;
                text-decoration: none;
                li{
                    list-style: none;
                    text-decoration: none;
                    min-height: 2.5rem;
                    min-width: 40%;
                }
             
                .call{
                    color: #585757;
                    font-size: 1rem;
                }
                .number{
                    font-size: 1.8rem;
                }
                .direction{
                    font-size: 1.1rem;
                }
                .second{
                    font-size: 1rem;
                    color: #585757;
                }
                .located{
                    font-size: 1.1rem;
                }
             
            
    }
    .navFooter{
        text-decoration: none;
        list-style: none;
        li{
            min-width: 10vw;
            font-size: 1rem;
            line-height: 2rem;
        }
        li:hover{
            color: #E5610E;
            
        }
    }

    
    .logoFooter{
        min-height: 30%;
        min-width: 40%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;       
    }
    .iconFooter{
        margin-top: 20px;
        width: 40%;
        img{
            margin-right: 10px;
        }
    }
}