
    .commonProblems{
        min-width: 100vw;
        min-height: 90vh;

        display: flex;
        flex-direction: row;
        .Problems{
            min-width: 30vw;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
            h2{
                margin-left:5vw;
                width: 80%;
                color: #E5610E;
                font-size: 3rem;
            }
            
            label{
                margin-left:5vw;
                width: 80%;
                display: flex;
                text{
                    cursor: pointer;
                    display: flex;
                    border: 1px solid #E5610E;
                    min-width: 8vw; 
                    min-height: 5vh;
                    border-radius: 2vw;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-top: 1.5vh;
                    color: #E5610E;
                    font-size: 1.2rem;
                }
            }
            .optionProblem{
                text{
                    background-color: #E5610E;
                    color: #FFF;
                }
               
            }
        }
        .Images{
            min-width: 70vw;
            display: flex;
            justify-content: flex-end;
            .Problema1,.Problema2,.Problema3,.Problema4,.Problema5,.Problema6,.Problema7{
                display: flex;
                min-width: 80%;
                max-width: 80%;
                min-height: 100%;
                
                background-size: 120%;
                background-repeat: no-repeat;            
            }
            .Problema1{background-image: url('../../../img/home/problems/Fondo0.png');}
            .Problema2{background-image: url('../../../img/home/problems/Fondo1.png');}
            .Problema3{background-image: url('../../../img/home/problems/Fondo2.png');}            
            .Problema4{background-image: url('../../../img/home/problems/Fondo3.png');}
            .Problema5{background-image: url('../../../img/home/problems/Fondo4.png');}            
            .Problema6{background-image: url('../../../img/home/problems/Fondo5.png');}
            .Problema7{background-image: url('../../../img/home/problems/Fondo6.png');}

        }
           
    }